.about {
  &-card {
    padding-top: 62px;
    padding-bottom: 50px;

    @media (max-width: 768px) {
      padding-bottom: 40px;

      &:last-of-type {
        padding-bottom: 60px;
      }
    }

    @media (max-width: 599px) {
      padding-top: 0;
    }

    &-top {
      @media (max-width: 768px) {
        order: -1;
      }
    }

    &-description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      max-width: 536px;
      margin-bottom: 30px;

      @media (max-width: 1280px) {
        height: unset;
      }

      @media (max-width: 768px) {
        max-width: unset;
        height: unset;
        margin-bottom: 0;
      }

      &__right {
        margin-left: auto;

        @media (max-width: 768px) {
          margin-left: 0;
        }
      }
    }

    &-title {
      font-weight: 600;
      @include fz(32);
      margin-bottom: 24px;

      &.mobile {
        display: none;

        @media (max-width: 768px) {
          display: inline-block;
        }
      }

      &.desktop {
        @media (max-width: 768px) {
          display: none;
        }
      }
    }

    &-text {
      color: $font-light;
    }

    &-image {
      aspect-ratio: 1.6;
      margin-bottom: 30px;

      @media (max-width: 768px) {
        margin-bottom: 24px;
      }

      img {
        border-radius: 10px;
        object-fit: cover;
        width: 100%;
        height: 100%;
        display: block;
      }
    }
  }

  &-description {
    // background-color: #F5F6F7;
    background-color: #fff;
    padding-top: 80px;
    padding-bottom: 80px;

    @media (max-width: 768px) {
      margin-bottom: 40px;
    }

    &-content {
      text-align: center;
      max-width: 872px;
      margin-left: auto;
      margin-right: auto;
    }

    &-title {
      font-weight: 600;
      @include fz(32);
      margin-top: 0;
      margin-bottom: 24px;
    }

    &-text {
      color: $font-light;
    }
  }
}

.slogan {
  text-align: center;
  @include fz(32);
  color: #f79e1b;
  max-width: 797px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 80px;
  padding-bottom: 80px;

  @media (max-width: 768px) {
    padding-top: 40px;
  }
}
