.plan {
  &-slider {
    margin-bottom: 12px;

    &-item {
      width: 100%;
      height: 100%;

      &-image {
        border-radius: 10px;
      }

      &.new {
        position: relative;
        &::after {
          position: absolute;
          left: -3px;
          top: -3px;
          content: "";
          background-image: url(../images/ticket-new.svg);
          background-repeat: no-repeat;
          width: 83px;
          height: 83px;
        }
      }

      &.sales {
        position: relative;
        &::after {
          position: absolute;
          left: -3px;
          top: -3px;
          content: "";
          background-image: url(../images/ticket-sales.svg);
          background-repeat: no-repeat;
          width: 83px;
          height: 83px;
        }
      }
    }

    .swiper {
      width: 100%;
      height: 100%;
    }

    .swiper-slide {
      text-align: center;
      @include fz(18);
      background: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .swiper-slide img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .swiper {
      width: 100%;
      height: 300px;
      margin-left: auto;
      margin-right: auto;
    }

    .swiper-slide {
      background-size: cover;
      background-position: center;
    }

    .planSwiper {
      // height: 80%;
      // width: 100%;
      aspect-ratio: 1.68;
      width: unset;
      height: unset;
      padding: 3px;
      margin: -3px;
    }

    .planSwiperPreview {
      height: 20%;
      box-sizing: border-box;
      padding: 20px 0;

      @media (max-width: 599px) {
        padding: 12px 0;
      }
    }

    .planSwiperPreview .swiper-slide {
      width: 25%;
      height: 100%;
      border-radius: 10px;
      overflow: hidden;
      cursor: $pointer;
    }

    .mySwiper .swiper-slide-thumb-active {
      opacity: 1;
    }

    .swiper-slide img,
    .swiper-slide iframe {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      aspect-ratio: 1.5;
      border-radius: 10px;
      border: none;
    }
  }

  .planSwiperPreview {
    .swiper-slide img,
    .swiper-slide iframe {
      pointer-events: none;
    }
  }

  &-main {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px;
    margin-bottom: 60px;

    @media (max-width: 599px) {
      margin-bottom: 28px;
    }

    &-left {
      flex: 0 0 52%;
      max-width: 52%;
      padding-left: 12px;
      padding-right: 12px;

      @media (max-width: 1366px) {
        flex: 0 0 60%;
        max-width: 60%;
        align-self: center;
      }

      @media (max-width: 1024px) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }

    &-right {
      flex: 0 0 48%;
      max-width: 48%;
      padding-left: 12px;
      padding-right: 12px;

      @media (max-width: 1366px) {
        flex: 0 0 40%;
        max-width: 40%;
      }

      @media (max-width: 1024px) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }

  &-license {
    max-width: 560px;
    margin-left: auto;

    @media (max-width: 1024px) {
      max-width: 100%;
    }

    &-action {
      display: flex;
      flex-wrap: wrap;
      gap: 18px;
      margin-bottom: 24px;

      &-btn {
        min-width: 205px;
        flex: 0 0 205px;

        @media (max-width: 1366px) {
          &.ui-btn__green {
            min-width: 100%;
            flex: 0 0 100%;
          }

          &.ui-btn__orange {
            flex-grow: 1;
          }
        }

        @media (max-width: 1024px) {
          &.ui-btn__green {
            min-width: 205px;
            flex: 0 0 205px;
          }

          &.ui-btn__orange {
            min-width: 205px;
            flex: 0 0 205px;
          }
        }

        @media (max-width: 768px) {
          &.ui-btn__green {
            min-width: 100%;
            flex: 0 0 100%;
          }

          &.ui-btn__orange {
            flex-grow: 1;
          }
        }
      }
    }

    &-category {
      padding: 24px;
      background: #e1e3e5;
      border: 1px solid $border;
      border-radius: 8px;
      margin-bottom: 24px;

      @media (max-width: 599px) {
        padding: 12px;
      }
    }
  }

  &-price {
    margin-bottom: 24px;

    &-current {
      display: inline-block;
      vertical-align: middle;
      @include fz(32);
      font-weight: 600;
      color: $title;
      margin-right: 14px;
    }

    &-discount {
      display: inline-block;
      vertical-align: middle;
      font-weight: 600;
      text-decoration-line: line-through;
      color: $title;
      opacity: 0.5;
    }
  }

  &-share {
    @media (max-width: 599px) {
      .plan-share-input {
        max-width: calc(100% - 140px);
        flex: 0 0 calc(100% - 140px);
      }

      .plan-share-socials {
        width: max-content;
        max-width: unset;
        flex: auto;
      }
    }
  }

  &-info {
    margin-bottom: 56px;

    @media (max-width: 599px) {
      margin-bottom: 40px;
    }

    &-description {
      p {
        line-height: 1.5;
        margin-bottom: 24px;
      }

      &-types {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        row-gap: 12px;
        margin-bottom: 24px;
      }

      &-label {
        font-weight: 600;
        margin-right: 30px;
        white-space: nowrap;
      }

      &-items {
        display: flex;
        gap: 10px;
      }

      &-item {
        padding: 10px 20px;
        background-color: #ffffff;
        border: 1px solid $border;
        border-radius: 50px;
        @include fz(14);
      }

      &-date {
        line-height: 1.5;
        color: $title;
        opacity: 0.5;
      }
    }

    &-reviews {
      &-header {
        margin-bottom: 40px;
      }
      &-title {
        @include fz(24);
        font-weight: 600;
        margin-bottom: 40px;
        @media (max-width: 599px) {
          margin-bottom: 24px;
        }
      }
      &-statistic {
        margin-bottom: 40px;
        @media (max-width: 599px) {
          margin-bottom: 24px;
        }
      }
      &-rating {
        display: flex;
        gap: 10px;
        margin-bottom: 10px;
      }
      &-stars {
        display: flex;
        gap: 5px;
      }
      &-icon {
        width: 20px;
        height: 20px;
        fill: #e1e3e5;
        margin-right: 5px;
        transition: fill 0.3s;
        &.active {
          fill: $secondary;
        }
      }
      &-content {
        .reviews-card-contents {
          margin-bottom: 24px;
          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }

      &-total {
        @media (max-width: 768px) {
          margin-top: 40px;
        }
        @media (max-width: 599px) {
          margin-top: 24px;
        }

        &-amount {
          @media (max-width: 1024px) {
            width: 20%;
          }
        }

        &-star {
          width: 55px;
        }
        &-space {
          width: 300px;
          height: 4px;
          border-radius: 5px;
          background-color: #d9d9d9;

          &::after {
            content: "";
            display: block;
            width: var(--width, 0);
            height: 4px;
            background-color: #000000;
            border-radius: 5px;
          }

          @media (max-width: 992px) {
            width: unset;
            flex: 1;
          }
        }
        &-line {
          display: flex;
          align-items: center;
          gap: 10px;
          margin-bottom: 6px;
        }
      }
    }

    &-use {
      &-video {
        margin: 24px 0;
        width: 567px;
        height: 300px;
        border-radius: 10px;

        @media (max-width: 768px) {
          width: 100%;
        }
        @media (max-width: 599px) {
          height: 200px;
        }
      }
    }
  }

  &-products {
    &-head {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: baseline;
      margin-bottom: 20px;
    }
  }
}
